export class Pts {
  [key: string]: number | Function;
  STR = 1; //
  AGI = 1; //
  INT = 1; //
  VIT = 1; //
  MEN = 1; //
  CON = 1; //

  load(value: Pts): Pts {
    if (value == null) return this;
    for (const key in value) {
      if (
        Object.prototype.hasOwnProperty.call(value, key) &&
        Object.prototype.hasOwnProperty.call(this, key)
      )
        this[key] = value[key];
    }
    return this;
  }

  toString(): string {
    return (
      this.STR +
      " " +
      this.AGI +
      " " +
      this.INT +
      " " +
      this.VIT +
      " " +
      this.MEN +
      " " +
      this.CON
    );
  }
}
